.nav-btns{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.btn{
    margin: 0px 15px;
}

.logo{
	padding-top: 10px;
}
.logo img{
	width: 250px;
}


.contact{
	padding-top:5px;
}

@media screen and (max-width:760px) {
	.nav-btns{
		display: none;
	}
	.contact{
		display: none;
	}
}


@media screen and (min-width:760px) {
	.drawer{
		display: none;
	}
	.sp-btn{
		display: none;
	}
}